<template>
	<view>
		<page-load v-if="pageshow"></page-load>
		<view v-else class="uni-padding-wrap uni-common-mt">
			<view style="background-color: #ffffff;">
				<uni-segmented-control :activeIndex="activeIndex" :values="items" style-type="text"
					active-color="#ff941d" @clickItem="onClickItem" />
			</view>
			<view class="content">
				<view class="order-list" v-if="orderListData.length>0">
					<view class="order-item" v-for="(item ,index) in orderListData" :key="index">
						<view class="head">
							<view class="order-number">
								<view class="icon">
									<image class="img" mode="widthFix"
										src="https://mkhotel.oss-cn-shanghai.aliyuncs.com/static/image/order-list-icon.png">
									</image>
								</view>
								<view class="number">{{item.orderId}}</view>
							</view>
							<view class="order-state">{{ item.state | payStatus }}</view>
						</view>
						<view class="body" @tap="openOrderDetail()">
							<view class="listings">
								<view class="picture">
									<image class="img" :v-if="item.thum" mode="widthFix" :src="item.thum">
									</image>
								</view>
								<view class="info">
									<view class="title">
										{{item.title}}
									</view>
									<view class="date">
										<view class="in-out">{{item.checkIn}}至{{item.checkOut}}</view>
									</view>
									<view class="status-desc">
										<text class="active">支付金额</text>
										<text class="txt" style="margin-left: 10rpx;">￥{{item.price}}</text>
									</view>
								</view>
							</view>
						</view>
						<view class="footer" v-if="item.state===0">
							<button class="btn-hollow" type="default" size="mini"
								@tap="cancelOrder(item.id)">取消订单</button>
							<button class="btn-solid" type="default" size="mini" @tap="payWX(item.id)">立即支付</button>
						</view>
						<view class="footer" v-if="item.state===1">
							<button class="btn-solid" type="default" size="mini" @tap="">联系客服</button>
						</view>
						<view class="footer" v-if="item.state===3">
							<button class="btn-hollow" type="default" size="mini" @tap="delOrder(item.id)">删除订单</button>
							<button class="btn-solid" type="default" size="mini" @tap="">再次预定</button>
						</view>
						<view class="footer" v-if="item.state===9">
							<button class="btn-hollow" type="default" size="mini" @tap="delOrder(item.id)">删除订单</button>
							<button class="btn-solid" type="default" size="mini" @tap="">再次预定</button>
						</view>
					</view>
				</view>
				<view class="none-data" v-else>
					<view class="img-signal">
						<image class="img" mode="widthFix" src="../../static/image/no-data.png">
						</image>
						<text>暂无订单</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		orderList,
		delOrder,
		cancelOrder,
		payWX,
	} from '@/utils/request/manage.js'
	import pageLoad from '@/components/pageLoad/pageLoad'
	export default {
		components: {
			pageLoad
		},
		data() {
			return {
				items: ['全部', '待付款', '待入住', '已完成'],
				activeIndex: 0,
				state: '',
				btnnum: 0,
				orderListData: [],
				// show_lists: '',
				pageshow: true,
			}
		},
		mounted() {
			// this.show_lists = this.orderListData
		},
		onLoad() {
			this.getOrderList()
		},
		filters: {
			payStatus(value) {
				let obj = {
					0: '待支付',
					1: '已支付',
					2: '支付失败',
					3: '用户取消',
					4: '平台取消',
					5: '已退款',
					9: '已完成',
				}
				return obj[value]
			},
		},
		watch: {
			activeIndex(value) {
				switch (value) {
					case 1:
						this.state = 0;
						break;
					case 2:
						this.state = 1;
						break;
					case 3:
						this.state = 9;
						break;

					default:
						this.state = '';
						break;
				}
				this.getOrderList(this.state)
				console.log("activeIndex", this.activeIndex)
			},
		},
		methods: {
			onClickItem(e) {
				this.btnnum = e
				if (this.activeIndex !== e.currentIndex) {
					this.activeIndex = e.currentIndex
					// if (this.activeIndex == 0) {
					// 	this.show_lists = this.orderListData
					// } else {
					// 	this.show_lists = []
					// 	for (let i = 0; i < this.orderListData.length; i++) {
					// 		if (this.orderListData[i].status == this.activeIndex) {
					// 			this.show_lists.push(this.orderListData[i])
					// 		}
					// 	}
					// }
				}
			},
			async getOrderList(id) {
				const {
					data: res
				} = await orderList(id)
				if (res.code == 1) {
					return this.$api.msg(res.msg)
				} else {
					this.orderListData = res.data.rs
					console.log("res", res)
					console.log("orderListData", this.orderListData)
				}
				this.pageshow = false
			},
			async delOrder(id) {
				this.pageshow = true
				const {
					data: res
				} = await delOrder(id)
				if (res.code == 1) {
					return this.$api.msg(res.msg)
				} else {

					console.log("res", res)

				}
				this.getOrderList()
				this.pageshow = false
			},
			async cancelOrder(id) {
				this.pageshow = true
				const {
					data: res
				} = await cancelOrder(id)
				if (res.code == 1) {
					return this.$api.msg(res.msg)
				} else {

					console.log("res", res)

				}
				this.getOrderList()
				this.pageshow = false
			},
			async payWX(id) {
				const {
					data: res
				} = await payWX(id)
				if (res.code == 1) {
					return this.$api.msg(res.msg)
				} else {
					// this.orderNumber= res

					console.log("res", "支付成功", res)
				}

			},
			openOrderDetail() {
				uni.navigateTo({
					url: '../order/order-detail'
				})
			},
		}
	}
</script>

<style scoped lang="scss">
	.order-list {
		margin: 30rpx;

		.order-item {
			width: 94%;
			background-color: #ffffff;
			border-radius: 8rpx;
			padding: 3%;
			margin-bottom: 30rpx;

			.head {
				padding-bottom: 20rpx;
				border-bottom: 2rpx #f5f5f5 solid;
				display: flex;
				justify-content: space-between;
				align-items: center;

				.order-number {
					display: flex;
					align-items: center;
					color: #666666;
					font-size: 24rpx;

					.icon {
						width: 30rpx;
						height: 30rpx;

						.img {
							width: 100%;
						}
					}
				}

				.order-state {
					color: #ff951d;
				}
			}

			.body {
				padding: 20rpx 0;
				border-bottom: 2rpx #f5f5f5 solid;

				.listings {
					display: flex;
					justify-content: space-between;

					.picture {
						width: 35%;
						height: 140rpx;
						overflow: hidden;
						border-radius: 8rpx;
						margin-right: 5%;

						.img {
							width: 100%;
						}
					}

					.info {
						width: 60%;

						.title {
							// text-overflow: -o-ellipsis-lastline;
							// overflow: hidden;
							// text-overflow: ellipsis;
							// display: -webkit-box;
							// -webkit-line-clamp: 2;
							// line-clamp: 2;
							// -webkit-box-orient: vertical;
							overflow: hidden;
							text-overflow: ellipsis;
							white-space: nowrap;
							font-weight: bold;
							color: #333333;
							font-size: 30rpx;
						}

						.date {
							color: #666666;
							font-size: 26rpx;
							padding: 10rpx 0;
						}

						.status-desc {
							color: #666666;
							font-size: 26rpx;

							.active {
								color: #ff951d;
							}
						}
					}
				}
			}

			.footer {
				padding-top: 20rpx;
				text-align: right;

				.btn-solid {
					border-radius: 60rpx;
					border: none;
					background-color: #333333;
					color: #ffffff;
					margin: 0 10rpx;
					line-height: 60rpx;
					font-size: 26rpx;
					border: none;
				}

				.btn-hollow {
					border-radius: 60rpx;
					border: none;
					background-color: transparent;
					color: #999999;
					border: 2rpx #cccccc solid;
					margin: 0 10rpx;
					line-height: 60rpx;
					font-size: 26rpx;
				}
			}

		}
	}

	.none-data {
		
		border-radius: 8rpx;
		padding: 20rpx;
		margin: 30rpx 0;
		text-align: center;

		.img-signal {
			width: 300rpx;
			height: 300rpx;
			margin: 0 auto;

			.img {
				width: 100%;
			}
		}

	}

	/deep/ button::after {
		border: none;
	}
</style>
